body {
  background-color: #E2F7FF;
  font-family: 'Libre Franklin', sans-serif;
  width: 80%;
  margin: 0 auto; }

a {
  color: #4A5999;
  text-decoration: none;
  width: 100%;
  height: 100%; }
  a#top-download, a#lower-download {
    pointer-events: none; }

.download-button {
  transition: background-color 0.25s;
  padding: 10px;
  border: 1px solid #4A5999;
  width: 200px;
  text-align: center; }
  .download-button:hover {
    background-color: #ffffff;
    cursor: pointer; }
